import React from 'react';
import Container from "@/components/shared/container";
import Image from "next/image";
import logo from "../../public/statics/logo.svg"


const Header = () => {
    return (
        <header className="w-full px-44 py-2 z-50 sticky top-0 bg-white border-b border-zinc-900/10" id='header'>
            <Container className="w-full px-4 sm:px-6 lg:px-8  flex items-center justify-between" >
                <div>
                    <Image src={logo} alt="Логотип сайта"  className=" w-8 h-8 w-full" priority/>
                </div>
                <div>
                    <span></span>
                </div>
            </Container>
        </header>
    );
};

export default Header;